    font-size: 100%
}

*,
:after,
:before {
    box-sizing: inherit
}

body {
    margin: 0;
    padding: 0;
    background: #fefefe;
    font-family: "nta", Helvetica, Roboto, Arial, sans-serif;
    font-weight: 400;
    line-height: 1.8;
    color: #0a0a0a;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale
}

img {
    display: inline-block;
    vertical-align: middle;
    max-width: 100%;
    height: auto;
    -ms-interpolation-mode: bicubic
}

textarea {
    height: auto;
    min-height: 50px;
    border-radius: 0
}

select {
    box-sizing: border-box;
    width: 100%;
    border-radius: 0
}

.map_canvas embed,
.map_canvas img,
.map_canvas object,
.mqa-display embed,
.mqa-display img,
.mqa-display object {
    max-width: none!important
}

button {
    padding: 0;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    border: 0;
    border-radius: 0;
    background: transparent;
    line-height: 1
}

[data-whatinput=mouse] button {
    outline: 0
}

pre {
    overflow: auto
}

.is-visible {
    display: block!important
}

.is-hidden {
    display: none!important
}

.row {
    max-width: 75rem;
    margin-right: auto;
    margin-left: auto
}

.row:after,
.row:before {
    display: table;
    content: " "
}

.row:after {
    clear: both
}

.row.collapse>.column,
.row.collapse>.columns {
    padding-right: 0;
    padding-left: 0
}

.row .row {
    margin-right: -.625rem;
    margin-left: -.625rem
}

@media print,
screen and (min-width:40em) {
    .row .row {
        margin-right: -.9375rem;
        margin-left: -.9375rem
    }
}

@media print,
screen and (min-width:64em) {
    .row .row {
        margin-right: -.9375rem;
        margin-left: -.9375rem
    }
}

.row .row.collapse {
    margin-right: 0;
    margin-left: 0
}

.row.expanded {
    max-width: none
}

.row.expanded .row {
    margin-right: auto;
    margin-left: auto
}

.row:not(.expanded) .row {
    max-width: none
}

.row.gutter-small>.column,
.row.gutter-small>.columns {
    padding-right: .625rem;
    padding-left: .625rem
}

.row.gutter-medium>.column,
.row.gutter-medium>.columns {
    padding-right: .9375rem;
    padding-left: .9375rem
}

.column,
.columns {
    width: 100%;
    float: left;
    padding-right: .625rem;
    padding-left: .625rem
}

@media print,
screen and (min-width:40em) {
    .column,
    .columns {
        padding-right: .9375rem;
        padding-left: .9375rem
    }
}

.column:last-child:not(:first-child),
.columns:last-child:not(:first-child) {
    float: right
}

.column.end:last-child:last-child,
.end.columns:last-child:last-child {
    float: left
}

.column.row.row,
.row.row.columns {
    float: none
}

.row .column.row.row,
.row .row.row.columns {
    margin-right: 0;
    margin-left: 0;
    padding-right: 0;
    padding-left: 0
}

